<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>基本资料</span>
    </div>
    <!-- 表单 -->
    <el-form
      :model="userForm"
      :rules="userFormRules"
      ref="userFormRef"
      label-width="100px"
    >
      <el-form-item label="登录名称" prop="username">
        <el-input v-model="userForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="用户类型" prop="usertyoe">
        <el-input v-model="userForm.usertype" disabled></el-input>
      </el-form-item>
      <el-form-item label="联系方式" prop="tel">
        <el-input v-model="userForm.tel"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitFn">提交修改</el-button>
        <el-button @click="reSet">重置</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>

<script>
import { updateUserInfoAPI } from "@/api";
export default {
  name: "UserInfo",
  data() {
    return {
      userForm: {
        id: this.$store.state.userInfo.uid,
        username: this.$store.state.userInfo.uname, // 默认值用登录后获取到的用户名
        usertype:
          this.$store.state.userInfo.flag == "c" ? "普通用户" : "管理用户",
        tel: this.$store.state.userInfo.tel,
      },
      // 表单的验证规则对象
      userFormRules: {
        tel: [
          { required: true, message: "请输入联系方式", trigger: "blur" },
          {
            pattern: /^[0-9]{11}$/,
            message: "请输入正确格式的联系方式！",
            trigger: "blur",
          },
        ],
      },
    };
  },
  //   ==============================================以下是method方法==============================================
  methods: {
    submitFn() {
      this.$refs.userFormRef.validate(async (valid) => {
        if (valid) {
          // 验证规则通过
          //   发起更新用户信息的请求
          const { data: res } = await updateUserInfoAPI({
            uid: this.userForm.id,
            tel: this.userForm.tel,
          });
          if (res.code !== 0) return this.$message.error("用户信息更新失败！");
          this.$message.success("用户信息更新成功！");
          //   更新vuex中的数据
          this.$store.dispatch("initUserInfo");
        } else {
          //   验证失败;
          return this.$message.error("数据填写有误，请请重新编辑！");
        }
      });
    },
    reSet() {
      this.$refs.userFormRef.resetFields();
    },
  },
  //   ==============================================以下是mounted方法==============================================
  mounted() {},
};
</script>

<style lang="less" scoped>
.el-form {
  width: 500px;
}
</style>
